import PropTypes from 'prop-types'
import React from 'react'
import pic01 from '../images/pic01.jpg'
import pic02 from '../images/pic02.jpg'
import pic03 from '../images/pic03.jpg'

class Main extends React.Component {
  render() {
    let close = (
      <div
        className="close"
        onClick={() => {
          this.props.onCloseArticle()
        }}
      ></div>
    )

    return (
      <div
        ref={this.props.setWrapperRef}
        id="main"
        style={this.props.timeout ? { display: 'flex' } : { display: 'none' }}
      >
        <article
          id="intro"
          className={`${this.props.article === 'intro' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}
        >
          <h2 className="major">Mon Parcours</h2>
          <p>
            Après mettre essayer aux études supérieurs, j'ai décidé de me mettre
            à travailler dans divers petits boulots. Puis le Cipecma m'a proposé
            une reconversion professionnelle afin de devenir Développeur Web/Web
            Mobile
          </p>
          <p>
            Aujourd'hui j'aime ce que je fais ce qui me permet de pouvoir
            m'épanouir dans ce que je fais, de rendre un travail de qualité à
            mes clients.
          </p>
          {close}
        </article>

        <article
          id="work"
          className={`${this.props.article === 'work' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}
        >
          <h2 className="major">Mon Développement Web</h2>
          <span className="image main">
            <img src={pic02} alt="" />
          </span>
          <p>Pour la création de site j'utilise :</p>
          <p>
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/512px-HTML5_logo_and_wordmark.svg.png"
              alt="HTML5"
              title="HTML5"
              width="50px"
            />
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/d/d5/CSS3_logo_and_wordmark.svg/130px-CSS3_logo_and_wordmark.svg.png"
              alt="CSS3"
              title="CSS3"
              width="35px"
            />
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/131px-Unofficial_JavaScript_logo_2.svg.png"
              alt="JavaScript"
              title="JavaScript"
              width="40px"
            />
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/27/PHP-logo.svg/131px-PHP-logo.svg.png"
              alt="PHP"
              title="PHP"
              width="50px"
            />
            <img
              src="https://upload.wikimedia.org/wikipedia/fr/thumb/6/62/MySQL.svg/langfr-220px-MySQL.svg.png"
              alt="MySQL"
              title="MySQL"
              width="70px"
            />
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/20/WordPress_logo.svg/langfr-220px-WordPress_logo.svg.png"
              alt="WordPress"
              title="WordPress"
              width="100px"
            />
          </p>
          <p>
              Mais je n'en reste pas là, je travail tous les jours afin de pouvoir proposer plus de service à mes clients.
          </p>
          {close}
        </article>

        <article
          id="about"
          className={`${this.props.article === 'about' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}
        >
          <h2 className="major">Mes Travaux</h2>
          <p>
            Étant fraichement arrivé sur le marché du travail en tant que developpeur Web Freelance, je suis à la recherche de mes premières missions.
          </p>

          {close}
        </article>

        <article
          id="contact"
          className={`${this.props.article === 'contact' ? 'active' : ''} ${
            this.props.articleTimeout ? 'timeout' : ''
          }`}
          style={{ display: 'none' }}
        >
          <h2 className="major">Contact</h2>
          <p>
              Vous souhaitez plus d'information sur les services que je peux mettre à votre disposition. Aucun problème envoyez moi un mail.
              J'essaierai de vous répondre dans les plus brefs délais.<br></br>
              <br></br>
            
              Adresse mail : contact@perdouxquentin.ovh
          </p>
          <a id="contact" href="mailto:contact@perdouxquentin.ovh">Envoyer un mail</a>
          {close}
        </article>
      </div>
    )
  }
}

Main.propTypes = {
  route: PropTypes.object,
  article: PropTypes.string,
  articleTimeout: PropTypes.bool,
  onCloseArticle: PropTypes.func,
  timeout: PropTypes.bool,
  setWrapperRef: PropTypes.func.isRequired,
}

export default Main
